//Font Family
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
//Font Family

//Variables
$main-color: #9C9EFE;
$main-text-color: #5800FF;
$default-transition-value: 400ms;
$default-transition-value-fast: 200ms;
//Variables

//Reset CSS
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
    list-style: none;
    color: #fff;
}
//Reset CSS

.active{
    li{
        &:before{
            width: 100%!important;
        }
        //color: $main-text-color;
    }
}

::webkit-scrollbar{
    width: 0px;
}

body{
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(to right, #80558C , #5800FF);
    position: relative;
    top: 0;
    margin: 0px auto;
    .box{
        max-width: 1200px;
        margin: 0px auto;
        background: rgba(0,0,0,0.1);
        min-height: 95vh;
        top: 17px;
        position: relative;
        border-radius: 13px;
        box-shadow: 0px 1px 5px rgba(0,0,0,0.3);
        border: 1px solid rgba(0,0,0,0.05);
        padding: 30px 40px;
        nav{
            width: 100%;
            height: 60px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            ul{
                display: flex;
                justify-content: flex-start;
                align-items: center;
                li{
                    position: relative;
                    margin: 0px 20px;
                    font-size: 13px;
                    font-weight: 400;
                    letter-spacing: 1px;
                    
                    &:before{
                        content: '';
                        position: absolute;
                        width: 0%;
                        height: 1.7px;
                        background: #fff;
                        bottom: 0;
                        border-radius: 20px;
                        transition: 300ms;
                        transition-delay: 150ms;
                    }
                    &:hover:before{
                        width: 100%;
                        left: 0;
                    }
                }
            }
            .nav-logo{
                position: relative;
                font-size: 30px;
                font-weight: 700;
                letter-spacing: 2px;
                font-style: italic;
                margin-right: 5%;
                cursor: no-drop;
                p{
                    font-size: 9px;
                    letter-spacing: 1px;
                    font-weight: 500;
                    font-style: italic;
                }
            }
        }
        .home{
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            width: auto;
            height: 100%;
            margin-top: 5%;
            h1{
                font-size: 65px;
                font-weight: 300;
                font-family: 'Pacifico', cursive;
            }
            h2{
                font-size: 50px;
                font-weight: 100;
            }
            h3{
                font-size: 30px;
                font-weight: 200;
                letter-spacing: 2px;
                im{
                    font-size: 50px;
                    font-weight: 500;
                    font-family: 'Pacifico', cursive;
                }
            }
            .home-buttons{
                margin-top: 5%;
                position: relative;
                span{
                    border: 1px solid #F1EFDC;
                    font-size: 18px;
                    padding: 10px 25px;
                    margin: 10px;
                    border-radius: 40px;
                    //background: rgba(0,0,0,0.1);
                    letter-spacing: 1px;
                    font-weight: 200;
                    transition: $default-transition-value-fast;
                    &:hover{
                        box-shadow: 0px 0px 3px #fff;
                        background: #fff;
                        color: $main-color;
                        font-weight: 400;
                    }
                }
            }
        }
        .tech{
            position: relative;
            width: 100%;
            height: auto;
            margin: 0px auto;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            h1{
                text-align: center;
                font-family: 'Pacifico', cursive;
                letter-spacing: 1px;
                font-size: 2vw;
                position: relative;
                margin: 1%;
                font-weight: 200;
            }
            ul{
                display: flex;
                flex-wrap: wrap;
                flex-direction: row;
                justify-content: space-between;
                li{
                    width: 200px;
                    height: 140px;
                    background: rgba(0,0,0,0.2);
                    margin-top: 40px;
                    border-radius: 10px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;
                    transition: $default-transition-value;
                    animation: slide $default-transition-value linear;
                    @mixin keyframes($name) {
                        @keyframes #{$name} {
                        @content;
                        }
                    }
                    @include keyframes(slide){
                        0%{
                            transform: scaleY(0);
                        }
                        100%{
                            transform: scaleY(1);
                        }
                    }
                    &:hover{
                        box-shadow: 0px 0px 7px rgba(0,0,0,0.2);
                        transform: translateY(-10px);
                        background: rgba(0,0,0,0);
                    }
                    img{
                        width: 45%;
                        height: auto;
                        border-radius: 7px;
                        box-shadow: 0px 1px 4px rgba(0,0,0,0.2);
                        padding: 10px;
                        transition: $default-transition-value;
                    }
                }
            }
        }
        .templates{
            position: relative;
            width: 100%;
            height: auto;
            h1{
                text-align: center;
                font-family: 'Pacifico', cursive;
                font-weight: 200;
                font-size: 2vw;
                margin: 1% auto;
            }
            ul{
                display: flex;
                justify-content: flex-start;
                margin-top: 20px;
                li{
                    width: 250px;
                    height: 350px;
                    overflow: hidden;
                    margin:0px 10px;
                    border-radius: 10px;
                    box-shadow: 0px 1px 10px rgba(0,0,0,0.4);
                    border: 2px solid rgba(0,0,0,0.4);
                    font-size: 30px;
                    display: grid;
                    place-items: center;
                    background: rgba(0,0,0,0.4);
                    animation: yscaling $default-transition-value linear;
                    @mixin keyframes($name){
                        @keyframes #{$name}{
                            @content
                        }
                    }
                    @include keyframes(yscaling){
                        0%{
                            transform: scaleX(0);
                        }
                        100%{
                            transform: scaleX(1);
                        }
                    }
                }
            }
        }
        .contact{
            position: relative;
            width: 100%;
            height: auto;
            h1{
                font-family: 'Pacifico', cursive;
                font-size: 2vw;
                font-weight: 200;
                text-align: center;
                margin: 20px 0px;
            }
            .contact-box{
                position: relative;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                ul{
                    position: relative;
                    width: 50%;
                    li{
                        width: 300px;
                        height: 60px;
                        line-height: 60px;
                        display: flex;
                        justify-content: flex-start;
                        align-items: center;
                        &:hover{
                            i{
                                box-shadow: 0px 0px 5px rgba(0,0,0,0.5);
                            }
                        }
                        i{
                            font-size: 40px;
                            width: 60px;
                            height: 60px;
                            //border: 1px solid rgba(0,0,0,0.2);
                            text-align: center;
                            line-height: 60px;
                            background: rgba(0,0,0,0.2);
                            transition: $default-transition-value-fast;
                        }
                        a{
                            font-size: 15px;
                            font-weight: 300;
                            letter-spacing: 1px;
                            padding: 0px 10px;
                        }
                    }
                }
                form{
                    position: relative;
                    width: 50%;
                    display: flex;
                    flex-direction: column;
                    input, .sug-input{
                        width: 300px;
                        height: 50px;
                        padding: 0px 20px;
                        font-size: 15px;
                        margin: 10px 0px;
                        outline: none;
                        color: #fff;
                        border: none;
                        transition: $default-transition-value-fast;
                        background: rgba(0,0,0,0.2);
                        &::placeholder{
                            color: $main-color;
                        }
                        &:focus{
                            box-shadow: 0px 0px 5px rgba(0,0,0,0.8);
                        }
                    }
                    .sug-input{
                        height: 100px;
                        padding: 10px 20px;
                    }
                    button{
                        position: relative;
                        font-size: 15px;
                        color: $main-color;
                        width: 120px;
                        height: 40px;
                        margin-top: 10px;
                        background: rgba(0,0,0,0.2);
                        border: none;
                        outline: none;
                        &:before{
                            content: "Send";
                            position: absolute;
                            top: 0;
                            left: 0;
                            background: #fff;
                            width: 100%;
                            height: 100%;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            clip-path: circle(0px at 50% 100%);
                            transition: $default-transition-value;
                            color: $main-text-color;
                            font-weight: 500;
                        }
                        &:hover:before{
                            clip-path: circle(100%);
                        }
                    }
                }
            }
        }
    }
}

